*,*::after,*::before{
    margin:0;
    padding:0;
    box-sizing: inherit;
    
}
body{
    font-family: "Lato",sans-serif;
    font-weight:400;
    font-size:1.6rem;
    line-height: 1.7;
    color:#777;
    padding:3rem;
    box-sizing: border-box; 
}
.header{
    margin-top:2rem;
    height:95vh;
    background-image: linear-gradient(to right bottom,
     rgba(126, 213, 111, 0.8), rgba(40, 180, 131,0.8)), url(../Images/bipan2.jpg);
    background-position: center;
    background-size: cover;
    clip-path: polygon(0 0,100% 0, 100% 75vh, 0 100%);
}
.header__text-box{
    position: absolute;
    top:40%;
    left:50%;
    transform:translate(-50%, -50%);
    text-align:center;
}
.heading-primary{
    color:white;
    text-transform: uppercase;
    margin-bottom: 5rem;

}
.heading-primary--main{
    display: block;
    font-size:6rem;
    font-weight: 400;
    letter-spacing:2.5rem;
    animation-name: moveInLeft;
    animation-duration: 5s;
    animation-timing-function: ease-out;
    margin-top:3rem;

}
.heading-primary--sub{
    display:block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.75rem;
    animation-name:moveInRight;
    animation-timing-function:ease-out;
    animation-duration: 5s;
}
@keyframes moveInLeft
{
    0%{
        opacity: 0;
        transform:translateX(-100px);
    }
    80%{
        transform: translateX(10px);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
@keyframes moveInRight
{
    0%{
        opacity: 0;
        transform:translateX(1rem);
    }
    80%{
        transform: translateX(-1rem);
    }
    100%{
        opacity: 1;
        transform: translate(0);
    }
}
.btn,
.btn:link,
.btn:visited{
    text-transform: uppercase;
    text-decoration: none;
    padding:1.5rem 4rem;
    display : inline-block;
    border-radius: 1rem;
    transition: all .2s;
    position:relative;
    border:none;
}
.btn-green{
    background-color:rgba(70, 206, 52, 0.753);
    color:white;
}
.btn-white{
    background-color: #fff;
    color: #777;
    font-weight: 900;
    letter-spacing: .7rem;
    font-size: larger;
}
.btn:hover{
    transform: translateY(-3px);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.btn:active,
.btn:focus{
    outline:none;
    transform: translateY(-1px);
    box-shadow: 0 5rem 1rem rgba( 0, 0, 0, 0.2);
}
.btn::after{
    content: " ";
    display:inline-block;
    height:100%;
    width: 100%;
    border-radius:1rem;
    position: absolute;
    top:0;
    left:0;
    z-index:-1;
    transition :all .4s;
}
.btn:hover::after{
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}
@keyframes moveInBottom {
    0%{
        opacity: 0;
        transform:translateY(3px);
    }
    100%{
        opacity: 1;
        transform:translate(0);
    }
}
.btn--animated{
    animation-name: moveInBottom;
    animation-duration: 7s;
    animation-timing-function:  ease-out;
    animation-delay: .75s;
    animation-fill-mode: backwards;
}
.btn-white::after{
    background-color: white;
}
